@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    position: absolute;

    width: 100vw;
    height: 100vh;
    overflow: hidden;

    font-family: 'Roboto', sans-serif;
}
.application {
    position: relative;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;

    width: 100%;
    height: 100%;
}

.application > .top-bar {
    width: 100%;
    min-height: 56px;

    padding: 16px;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-align-items: center;
            align-items: center;

    box-shadow: 0 0 8px 0 #0008;
    z-index: 999;
}

.application > .top-bar > .toggle-menu {
    width: 24px;
    height: 24px;

    margin-right: 32px;

    background-color: transparent;
    border: none;
}

@media (min-width: 1200px) {
    .application > .top-bar > .toggle-menu {
        display: none;
    }
}

.application > .top-bar > .title {
    font-size: 1.17em;
}

@media (max-width: 1200px) {
    .application > .content > .dim {
        width: 100%;
        height: 100%;
        background-color: #0004;

        position: absolute;

        z-index: 100;
    }
}

.application > .content {
    width: 100%;
    height: 100%;
    overflow: hidden;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.application > .content > .sidebar {
    min-width: 228px;
    height: 100%;

    overflow-y: auto;
    overflow-x: hidden;

    padding: 6px;

    z-index: 200;

    border-right: solid 1px #0002;
    background-color: white;
}

@media (max-width: 1200px) {
    .application > .content > .sidebar {
        position: absolute;

        transition: -webkit-transform 60ms ease;

        transition: transform 60ms ease;

        transition: transform 60ms ease, -webkit-transform 60ms ease;
    }

    .application > .content > .sidebar.open {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }

    .application > .content > .sidebar.closed {
        -webkit-transform: translate(-100%, 0);
                transform: translate(-100%, 0);
    }
}

.page {
    width: 100%;
    height: 100%;
}
.navigation-menu-item {
    width: 100%;
    padding: 6px;
    margin-top: 6px;

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: center;
            align-items: center;

    cursor: pointer;

    color: #a29bfe;
}

.navigation-menu-item > .title {
    margin-left: 12px;
    font-size: 1rem;
}

.navigation-menu-item.selected {
    border-radius: 8px;

    background-color: #a29bfe44;
    color: #6c5ce7;
}


.home-page {

}
.video-preview {
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-flex-direction: column;
            flex-direction: column;

    padding: 12px;
}

.text {
    border-radius: 666px;
    height: 0.7em;
}

.item {
    background-color: lightgrey;
}

.video-preview > .thumbnail {
    width: 228px;
    height: 128px;
    border-radius: 6px;
}

.video-preview > .info {
    width: 100%;
    padding-top: 12px;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.video-preview > .info > .left > .avatar {
    width: 32px;
    height: 32px;
    border-radius: 50%;
}

.video-preview > .info > .right {
    width: 100%;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;

    padding-left: 8px;
}

.video-preview > .info > .right > .title {
    width: 80%;
}

.video-preview > .info > .right > .channel-name {
    width: 40%;
    margin-top: 8px;
}

.video-preview > .info > .right > .stats {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-top: 4px;
}

.video-preview > .info > .right > .stats > .text {
    width: 30%;
    margin-right: 4px;
}

.video-preview > .info > .right > .stats:first-child {
    margin-right: 0;
}
.search-page {
    padding: 12px;

    width: 100%;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.search-page > .search {
    border-radius: 12px;
    border: solid 1px #0002;
    padding: 8px;
    font-size: 0.75em;
    width: 50%;
}
form {
    margin-top: 24px;
}

.settings > .title {
    font-size: 1em;
    margin-bottom: 12px;
}

.settings > .checkbox {
    width: 100%;
    margin-top: 6px;
}

.settings > .checkbox > input {
    font-size: 1em;
}

.settings > .checkbox > label {
    font-size: 0.75em;
    margin-left: 12px;
}

#enable-all-checkbox {
    margin-bottom: 12px;
}
